ul {
  list-style: none;
}

.stats {
  list-style: none;

  display: flex;
  justify-content: space-around;
}

.stat-list {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.item {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 50px;
  height: 50px;
  margin-right: 20px;
  border: 2px solid rebeccapurple;
}

